import {
	mapState
} from 'vuex';
//import uniNumberBox from '@/components/uni-number-box.vue'
import tuiNumberbox from "@/components/numberbox/numberbox"
export default {
	components: {
		//uniNumberBox,
		tuiNumberbox
	},
	data() {
		return {
			getimg:this.$getimg,
			allChecked: false, //全选状态  true|false
			empty: false, //空白页现实  true|false
			cartList: [],
		};
	},
	onLoad() {  
	},
	onShow() {
		this._load()
	},
	watch: { 
	},
	computed: { 
		//计算总价
		total() {
			let total = 0; 
			let list = Object.values(this.cartList);
			if (list.length === 0) {
				this.empty = true;
				return total;
			}
			let checked = true;
			list.forEach(item => {
				if (item.radio === true) {
					total += item.price * item.num;
				}  
			}) 
			total = Number(total.toFixed(2));
			return total;
		}
	},
	methods: {		
		_load() {
			let cache = uni.getStorageSync('cart')
			console.log('cache',cache)
			if (!cache) {
				return;
			} else { 
				this.cartList = cache;
				console.log(this.cartList)
			}
		},
		jump_detail(id){
			uni.navigateTo({
				url:'../extend-view/productDetail/productDetail?id='+id
			})
		},
		//数量
		numberChange(e,index) {
			console.log('num:', e.value,index)
			this.$set(this.cartList[index],'num',e.value)
			uni.removeStorageSync('cart')
			this.cartList
			uni.setStorageSync('cart',this.cartList) 
		}, 		
		// 监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		navToLogin() {
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		//选中状态处理
		_radio(type, index) {
			if (type === 'item') {
				//单选 
				this.cartList[index].radio = !this.cartList[index].radio;
				uni.removeStorageSync('cart')
				uni.setStorageSync('cart',this.cartList)
			} else {
				//全选
				const checked = !this.allChecked
			    let list = this.cartList;
				list.forEach(item => {
					item.radio = checked;   
				})
				this.allChecked = checked;
			} 
		},
		
		//删除
		deleteCartItem(index) {  
			let cartList=this.cartList 
			delete cartList[index]; 
			this.cartList=[]
			this.cartList=cartList 
			uni.setStorageSync('cart',cartList)  
		},
		//清空
		clearCart() {
			uni.showModal({
				content: '清空购物车？',
				success: (e) => {
					if (e.confirm) {
						this.cartList = '';
						uni.removeStorageSync('cart')
					}
				}
			})
		},
		
		//创建订单
		createOrder() { 				
			let buy_data = uni.getStorageSync('cart') 
			let x=0 
			for(let k in buy_data){
				const v=buy_data[k] 
				if(v.radio){ 
					x++
				} 
			}
			if(x>0){
				uni.navigateTo({
					url: '/pages/order/createOrder?state=car'
				})
			}				
		}
	},
	onPullDownRefresh() {
		this._load()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
